*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
html {
    font-size: 10px; }
body {
    font-family: "roboto";
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    padding: 5rem;
    background-color: white; }
.container {
    background-color: #F7F7F7; }
.header {
    height: 95vh;
    background: linear-gradient(287deg, rgba(216, 216, 216, 0.80) 0%, rgba(38, 32, 24, 0.80) 100%), url(../img/header-background.jpg) center/cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    // clip-path: polygon(0 33%, 60% 33%, 60% 0, 100% 50%, 100% 50%, 60% 100%, 60% 66%, 0 66%)

    &__logo {
        width: 5.6rem;
        height: 7rem;
        margin-top: 5rem;
        margin-left: 5rem; }
    &__box {

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }


.title-first {
    &__main {
        display: block;
        color: white;
        font-size: 5rem;
        font-family: "roboto";
        font-weight: 800;
        letter-spacing: 1rem;
        margin-bottom: 1rem;
        animation-name: leftToRight;
        animation-duration: 2s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }
    &__secondary {
        display: block;
        color: white;
        font-size: 4rem;
        font-family: "roboto";
        font-weight: 200;
        letter-spacing: 0.5rem;
        margin-bottom: 5rem;
        animation-name: rightToLight;
        animation-duration: 2s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }

