 }    //     letter-spacing: 5px
.btn {
    &:link, &:visited {
        text-decoration: none;
        text-transform: uppercase;
        color: #777;
        padding: 1.5rem 4rem;
        background: white;
        border-radius: 5rem;
        display: inline-block;
        transition: all .5s;
        position: relative;
        font-size: 2rem;
        &:hover {
            transform: translateY(-0.4rem);
            box-shadow: (0px .4rem .4rem rgba(0,0, 0, 0.5 ));
            &::after {
                transform: scaleX(1.5) scaleY(1.7);
                opacity: 0; } }

        &:active {
            transform: translateY(-2px);
            box-shadow: (0px .4rem .4rem rgba(0,0, 0, 0.2 )); }
        &::after {
            content: "";
            background: white;
            width: 100%;
            height: 100%;
            border-radius: 5rem;
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            transition: all .4s; } }
    &_animated {
        animation: BtnToUp .5s ease-out .7s;
        animation-fill-mode: backwards; } }


@keyframes BtnToUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }

    100% {
        opacity: 1;
        transform: translateY(0px); } }

@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    80% {
        transform: translateX(2rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes rightToLight {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    80% {
        transform: translateX(-2rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

